const Evento = props => {
  const { name, url, description } = props.info;
  let desc = description.text;
  if (desc) {
    desc = desc.substr(0, 250);
  }
  return (
    <a href={url} target="_blank" className="list-group-item  mb-1">
      <h4 className="mb-3">{name.text}</h4>
      <p className="mb-1">
        {desc}
        {"...."}
      </p>
    </a>
  );
};

export default Evento;
